.catalog-element {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 120px;
  height: 120px;
  border-radius: 75%;
  cursor: pointer;
  background-size: cover;
  border: 5px solid green;
}

@media screen and (max-width: 480px) {
  .catalog-element {
    width: 80px;
    height: 80px;
  }
}
